import React from 'react';
import './style.css';
import Confetti from 'react-confetti';

export class Results extends React.Component {
  render() {
    var balk1 = this.props.collega1;
    var balk2 = this.props.collega2;
    var balk3 = this.props.collega3;
    var balk4 = this.props.collega4;
    var balk5 = this.props.collega5;
    var balk6 = this.props.collega6;
    var aantalvragen = this.props.aantalVragen
    var procent = 100/aantalvragen;

    balk1 = balk1*procent+"%"
    balk2 = balk2*procent+"%"
    balk3 = balk3*procent+"%"
    balk4 = balk4*procent+"%"
    balk5 = balk5*procent+"%"
    balk6 = balk6*procent+"%"

    var naamCollega1 = this.props.naamCollega1;
    var naamCollega2 = this.props.naamCollega2;
    var naamCollega3 = this.props.naamCollega3;
    var naamCollega4 = this.props.naamCollega4;
    var naamCollega5 = this.props.naamCollega5;
    var naamCollega6 = this.props.naamCollega6;

    var display1 = "flex";
    var display2 = "flex";
    var display3 = "flex";
    var display4 = "flex";
    var display5 = "flex";
    var display6 = "flex";
    var displayconfetti = "none";

    if (naamCollega1 === "") {
      display1="none";
    }

    if (naamCollega2 === "") {
      display2="none";
    }

    if (naamCollega3 === "") {
      display3="none";
    }

    if (naamCollega4 === "") {
      display4="none";
    }

    if (naamCollega5 === "") {
      display5="none";
    }

    if (naamCollega6 === "") {
      display6="none";
    }

    if (this.props.nummerVraag === 12) {
      displayconfetti="block"
    }

    return (
      <div className="resultaten section">
        <Confetti style={{display: displayconfetti}}/>
        <div className="namen">
          <div className="eenlijnQuiz" style={{display: display1}}>
            <h4>{this.props.naamCollega1}</h4>
          </div>
          <div className="eenlijnQuiz" style={{display: display2}}>
            <h4>{this.props.naamCollega2}</h4>
          </div>
          <div className="eenlijnQuiz" style={{display: display3}}>
            <h4>{this.props.naamCollega3}</h4>
          </div>
          <div className="eenlijnQuiz" style={{display: display4}}>
            <h4>{this.props.naamCollega4}</h4>
          </div>
          <div className="eenlijnQuiz" style={{display: display5}}>
            <h4>{this.props.naamCollega5}</h4>
          </div>
          <div className="eenlijnQuiz" style={{display: display6}}>
            <h4>{this.props.naamCollega6}</h4>
          </div>
        </div>
        <div className="balken">
          <div className="eenlijnQuiz" style={{display: display1}}>
            <div className="balkQuiz" id="balk1" style={{width: balk1}}><p>{this.props.collega1}</p></div>
          </div>
          <div className="eenlijnQuiz" style={{display: display2}}>
            <div className="balkQuiz" id="balk2" style={{width: balk2}}><p>{this.props.collega2}</p></div>
          </div>
          <div className="eenlijnQuiz" style={{display: display3}}>
            <div className="balkQuiz" id="balk3"  style={{width: balk3}}><p>{this.props.collega3}</p></div>
          </div>
          <div className="eenlijnQuiz" style={{display: display4}}>
            <div className="balkQuiz" id="balk4"  style={{width: balk4}}><p>{this.props.collega4}</p></div>
          </div>
          <div className="eenlijnQuiz" style={{display: display5}}>
            <div className="balkQuiz" id="balk5"  style={{width: balk5}}><p>{this.props.collega5}</p></div>
          </div>
          <div className="eenlijnQuiz"  style={{display: display6}}>
            <div className="balkQuiz" id="balk6"  style={{width: balk6}}><p>{this.props.collega6}</p></div>
          </div>
        </div>
      </div>
    )
  }
}
