import React from 'react';
import './style.css';
import {Quizvraag} from './Quizvraag';
import { Link } from "react-router-dom";

export class Quizlijst extends React.Component {
  render() {
      if (this.props.gedaan === 0) {
        var vraagstelling = "block"
        var gedaan = "none"

      } else {
        var vraagstelling = "none"
        var gedaan = "block"
      }

      if (this.props.vorigeZichtbaar === 0) {
        var vorige = "none"

      } else {
        var vorige = "block"
      }

    return (
      <div className="section">
        <div style={{display: vraagstelling}} className="part-quiz">
          <Quizvraag
            nummer={this.props.nummerVraag}
            vragenlijst={this.props.vragenlijst}
            antwoordZichtbaar={this.props.antwoordZichtbaar}
            antwoordVerbergen={this.props.antwoordVerbergen}
            naamCollega1={this.props.naamCollega1}
            naamCollega2={this.props.naamCollega2}
            naamCollega3={this.props.naamCollega3}
            naamCollega4={this.props.naamCollega4}
            naamCollega5={this.props.naamCollega5}
            naamCollega6={this.props.naamCollega6}
            />
          <div className="oneline">
            <div id="startknop" onClick={this.props.vorigeVraag} style={{display: vorige}}>Vorige Vraag</div>
            <div id="startknop" onClick={this.props.indienen}>Antwoorden Indienen</div>
            <div id="startknop" onClick={this.props.volgendeVraag}>Volgende Vraag</div>
          </div>
        </div>
        <div style={{display: gedaan}} className="part-quiz">
          <h1>Quiz is gedaan!</h1>
          <Link to="/quiz/resultaten"><div id="startknop">Resultaten</div></Link>
        </div>
      </div>
    )
  }
}
