import React from 'react';
import './style.css';

export class Quizvraag extends React.Component {
  render() {
    var vraagnummer = this.props.nummer
    var vragenlijst = this.props.vragenlijst

    if (vragenlijst[vraagnummer-1].juist === 1){
      var juist = vragenlijst[vraagnummer-1].antw1
    } else if (vragenlijst[vraagnummer-1].juist === 2){
      var juist = vragenlijst[vraagnummer-1].antw2
    } else if (vragenlijst[vraagnummer-1].juist === 3){
      var juist = vragenlijst[vraagnummer-1].antw3
    } else if (vragenlijst[vraagnummer-1].juist === 4){
      var juist = vragenlijst[vraagnummer-1].antw4
    } else {
      var juist = "geen juist antwoord. Punten voor iedereen!"
    }

    if (this.props.antwoordZichtbaar === 0) {
      var antwoordZichtbaar = "none"
    } else {
      var antwoordZichtbaar = "block"
    }

    var naamCollega1 = this.props.naamCollega1;
    var naamCollega2 = this.props.naamCollega2;
    var naamCollega3 = this.props.naamCollega3;
    var naamCollega4 = this.props.naamCollega4;
    var naamCollega5 = this.props.naamCollega5;
    var naamCollega6 = this.props.naamCollega6;

    var display1 = "flex";
    var display2 = "flex";
    var display3 = "flex";
    var display4 = "flex";
    var display5 = "flex";
    var display6 = "flex";

    if (naamCollega1 === "") {
      display1="none";
    }

    if (naamCollega2 === "") {
      display2="none";
    }

    if (naamCollega3 === "") {
      display3="none";
    }

    if (naamCollega4 === "") {
      display4="none";
    }

    if (naamCollega5 === "") {
      display5="none";
    }

    if (naamCollega6 === "") {
      display6="none";
    }

    return (
      <div className="quizvraag">
        <h1>Vraag {vraagnummer}</h1>
        <h4>{vragenlijst[vraagnummer-1].vraag}</h4>
        <ol type="A" className="antvraag">
          <li>{vragenlijst[vraagnummer-1].antw1}</li>
          <li>{vragenlijst[vraagnummer-1].antw2}</li>
          <li>{vragenlijst[vraagnummer-1].antw3}</li>
          <li>{vragenlijst[vraagnummer-1].antw4}</li>
        </ol>
        <div className="inputantwoorden">
          <div className="antwoordlijn" style={{display: display1}}>
            <p className="antwoord">{this.props.naamCollega1}</p>
            <select id="antwoordCol1">
              <option value="1">A</option>
              <option value="2">B</option>
              <option value="3">C</option>
              <option value="4">D</option>
            </select>
          </div>
          <div className="antwoordlijn" style={{display: display2}}>
            <p className="antwoord">{this.props.naamCollega2}</p>
            <select id="antwoordCol2">
              <option value="1">A</option>
              <option value="2">B</option>
              <option value="3">C</option>
              <option value="4">D</option>
            </select>
          </div>
          <div className="antwoordlijn" style={{display: display3}}>
            <p className="antwoord">{this.props.naamCollega3}</p>
            <select id="antwoordCol3">
              <option value="1">A</option>
              <option value="2">B</option>
              <option value="3">C</option>
              <option value="4">D</option>
            </select>
          </div>
          <div className="antwoordlijn" style={{display: display4}}>
            <p className="antwoord">{this.props.naamCollega4}</p>
            <select id="antwoordCol4">
              <option value="1">A</option>
              <option value="2">B</option>
              <option value="3">C</option>
              <option value="4">D</option>
            </select>
          </div>
          <div className="antwoordlijn" style={{display: display5}}>
            <p className="antwoord">{this.props.naamCollega5}</p>
            <select id="antwoordCol5">
              <option value="1">A</option>
              <option value="2">B</option>
              <option value="3">C</option>
              <option value="4">D</option>
            </select>
          </div>
          <div className="antwoordlijn" style={{display: display6}}>
            <p className="antwoord">{this.props.naamCollega6}</p>
            <select id="antwoordCol6">
              <option value="1">A</option>
              <option value="2">B</option>
              <option value="3">C</option>
              <option value="4">D</option>
            </select>
          </div>
        </div>

        <h5 style={{display: antwoordZichtbaar}} id="juistAntwoord">Juiste antwoord: {juist}</h5>
      </div>
    )
  }
}
