import React from 'react';
import './App.css';
import Highcharts from 'highcharts'
import HighchartsReact from 'highcharts-react-official'
import belg from './belgie.png'

class Corona extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items: [],
      belgie: []
    }
  }

  componentDidMount() {
    this.getData();
 }

 componentWillUnmount() {
     clearTimeout(this.intervalID);
   }

 getData = () => {
   fetch("https://api.thevirustracker.com/free-api?countryTimeline=BE")
     .then(res => res.json())
     .then(
       (result) => {
         var obj = result.timelineitems
         this.setState({
           isLoaded: true,
           items: obj[0],
         })
       },
       // Note: it's important to handle errors here
       // instead of a catch() block so that we don't swallow
       // exceptions from actual bugs in components.
       (error) => {
         this.setState({
           isLoaded: true,
           error
         });
       }
     )

     fetch("https://api.thevirustracker.com/free-api?countryTotal=BE")
       .then(res => res.json())
       .then(
         (result) => {
           this.setState({
             isLoaded: true,
             belgie: result.countrydata,
           });
           this.intervalID = setTimeout(this.getData.bind(this), 100000);
         },
         // Note: it's important to handle errors here
         // instead of a catch() block so that we don't swallow
         // exceptions from actual bugs in components.
         (error) => {
           this.setState({
             isLoaded: true,
             error
           });
         }
       )
 }

  render() {
    const { error, isLoaded, items, belgie } = this.state;
    var totaalBesmet = belgie.map(a => a.total_cases)
    var totaalHersteld = belgie.map(a => a.total_recovered)
    var totaalDoden = belgie.map(a => a.total_deaths)
    var totaalNogZiek = totaalBesmet - totaalHersteld - totaalDoden
    var vandaagBesmet = belgie.map(a => a.total_new_cases_today)
    var vandaagDood = belgie.map(a => a.total_new_deaths_today)
    var arrayHistorie = Object.entries(items)
    var historie = arrayHistorie.map(a => a[1].total_cases)
    var dodental = arrayHistorie.map(a => a[1].total_deaths)
    var dertigHistorie = historie.slice(Math.max(historie.length - 30, 0))
    var dertigDodental = dodental.slice(Math.max(dodental.length - 30, 0))

    const options = {
      title: {
        text: 'Besmettingen in Belgie (laatste 30 dagen)'
      },
      series: [{
        name: 'België',
        data: dertigHistorie
      }],
      xAxis: {
        visible: false
      },
      chart: {
        type: 'line',
        width: 500,
        height: 400
      }
    }

    const optionsdoden = {
      title: {
        text: 'Doden in Belgie (laatste 30 dagen)'
      },
      series: [{
        name: 'België',
        data: dertigDodental
      }],
      xAxis: {
        visible: false
      },
      chart: {
        type: 'line',
        width: 500,
        height: 400
      }
    }

    const optionsTotal = {
      title: {
        text: 'Belgie sinds 23/01/20'
      },
      series: [{
        name: 'Besmettingen',
        data: dertigHistorie
      },{
        name: 'Doden',
        data: dertigDodental
      }],
      xAxis: {
        visible: false
      },
      chart: {
        type: 'line',
        width: 500,
        height: 400
      }
    }

    if (error) {
      return <div><header className="App-header">Error: {error.message}</header></div>;
    } else if (!isLoaded) {
      return <div><header className="App-header">Loading...</header></div>;
    } else {
  return (
    <div className="App">
      <header className="App-header-corona">
        <h1 id="corona">Corona in België</h1>
        <img src={belg} alt="BE" id="belgie"/>
          <div className="gegevens">
              <div className="inline">
                <h3>Totaal besmet</h3>
                <h4 className="red">{totaalBesmet}</h4>
              </div>
              <div className="inline">
                <h3>Totaal Hersteld</h3>
                <h4 className="green">{totaalHersteld}</h4>
              </div>
              <div className="inline">
                <h3>Totaal gestorven</h3>
                <h4 className="red">{totaalDoden}</h4>
              </div>
              <div className="inline">
                <h3>Totaal nog ziek</h3>
                <h4 className="red">{totaalNogZiek}</h4>
              </div>
              <div className="inline">
                <h3> </h3>
                <h4 className="red"> </h4>
              </div>
              <div className="inline">
                <h3>Vandaag besmet</h3>
                <h4 className="red">+ {vandaagBesmet}</h4>
              </div>
              <div className="inline">
                <h3>Vandaag gestorven</h3>
                <h4 className="red">+ {vandaagDood}</h4>
              </div>
          </div>

        <div className="chartDisplay">
          <h1>Grafieken</h1>
          <div className="chartGroot"><HighchartsReact highcharts={Highcharts} options={options}/></div>
          <div className="chartGroot"><HighchartsReact highcharts={Highcharts} options={optionsdoden}/></div>
          <div className="chartGroot"><HighchartsReact highcharts={Highcharts} options={optionsTotal}/></div>
        </div>
      </header>
    </div>
  )};
}}

export default Corona;
